  font-weight: 600;
  color: #111827;
  margin: 0;
}
.modal-header .close-btn {
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 0.375rem;
  transition: all 0.2s;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-header .close-btn:hover:not(:disabled) {
  background-color: #f3f4f6;
  color: #374151;
}
.modal-header .close-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.modal-header .close-btn svg {
  width: 1.25rem;
  height: 1.25rem;
}

.permission-denied {
