  border-top: 1px solid #e5e7eb;
}

.btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
}
.btn.btn-primary {
  background-color: #3b82f6;
  color: white;
}
.btn.btn-primary:hover:not(:disabled) {
  background-color: #2563eb;
}
.btn.btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.btn.btn-secondary {
  background-color: #6b7280;
  color: white;
}
.btn.btn-secondary:hover:not(:disabled) {
  background-color: #4b5563;
}
.btn.btn-outline {
  background-color: white;
  border: 1px solid #d1d5db;
  color: #374151;
}
.btn.btn-outline:hover:not(:disabled) {
  background-color: #f9fafb;
  border-color: #9ca3af;
}
.btn svg {
  width: 1rem;
  height: 1rem;
}

.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  border-radius: 0.75rem;
}
.loading-overlay .loading-spinner {
  text-align: center;
}
.loading-overlay .loading-spinner svg {
  color: #3b82f6;
}
.loading-overlay .loading-spinner .loading-text {
  margin-top: 1rem;
  color: #6b7280;
  font-weight: 500;
}

@media (max-width: 768px) {
  .modal-container {
    max-width: 100%;
    margin: 0.5rem;
  }
  .summary-cards {
    grid-template-columns: 1fr;
  }
  .invalid-rows-table-container {
    font-size: 0.75rem;
  }
  .modal-footer {
    flex-direction: column;
  }
  .modal-footer .btn {
    width: 100%;
    justify-content: center;
  }
}
